<template>
    <div>
        <Modal ref="modal" :item="item" />
        <v-list-item ripple>
            <v-btn @click="borrarDocumento" text icon color="red lighten-2">
                <v-icon>delete_forever</v-icon>
            </v-btn>
            <v-btn @click="abrirModal" text icon color="green lighten-2">
                <v-icon>edit</v-icon>
            </v-btn>
            <v-list-item-content>
                <v-list-item-title class="text--primary">{{ item.nombre }}</v-list-item-title>
                <v-list-item-subtitle class="text--primary">{{ item.expira }}</v-list-item-subtitle>
                <v-list-item-subtitle class="text--primary">{{ faltan(item.expira) }}</v-list-item-subtitle>
            </v-list-item-content>
            <v-list-item-action>
                <v-list-item-action-text>{{ item.id }}</v-list-item-action-text>
                <v-icon v-if="!expirado(item.expira)" color="green lighten-1">done_outline</v-icon>
                <v-icon v-else color="red lighten-1">warning</v-icon>
            </v-list-item-action>
        </v-list-item>
        <v-divider :key="`divider-${item.id}`"></v-divider>
    </div>
</template>


<script>
import moment from 'moment'
import Modal from './Modal'
export default {
    name:"Item",
    components:{
        Modal,
    },
    props:["item", "items"],
    data() {
        return {
            
        }
    },
    methods: {
        borrarDocumento(){
            let id = this.item.id;
            try {
                this.$store.commit("mostrarLoading", "Cargando Item");
                this.$store.commit("borrarDoc", {"id": id});
                //this.$store.commit("setItems");
            } catch (error) {
                console.error(error);
            } finally {
                this.$store.commit("ocultarLoading");
            }
        },
        expirado(fecha){
            return Date.parse(fecha) <= Date.now() ? true : false;
        },
        faltan(fecha){
            let vence = moment(fecha);
            const hoy = moment(Date.now());
            const annios = vence.diff(hoy, "year");
            hoy.add(annios, "years");
            const meses = vence.diff(hoy, "months");
            hoy.add(meses, "months");
            const dias = vence.diff(hoy, "days");
            hoy.add(dias, "days")
            const rs = annios + " años " + meses + " meses " + dias + " dias ";
            return this.expirado(fecha) ? "Expiró hace " + rs.replace(/-/g, "") : "Faltan : " + rs;

        },
        abrirModal(){
            this.$refs.modal.show();
        }
    },
    computed:{

    }
}
</script>


